'use strict';

import React from 'react';
import Router, {Route, NotFoundRoute, DefaultRoute, Redirect} from 'react-router';

import Base from './components/Base';
import DocumentList from './components/DocumentList';
import DocumentFormContainer from './components/DocumentFormContainer';
import DocumentPreview from './components/DocumentPreview';
import NotFound from './components/NotFound';

var Routes = [
  React.createElement(Route, {name: "base", handler: Base}, 
    React.createElement(Redirect, {from: "/", to: "/documents"}), 
    React.createElement(Route, {name: "list", path: "/documents", handler: DocumentList}), 
    React.createElement(Route, {name: "form_create", path: "/documents/create", handler: DocumentFormContainer}), 
    React.createElement(Route, {name: "preview", path: "/documents/:id", handler: DocumentPreview}), 
    React.createElement(Route, {name: "form", path: "/documents/:id/edit", handler: DocumentFormContainer})
  ),
  React.createElement(NotFoundRoute, {handler: NotFound})
];

Router.run(Routes, function (Handler) {
  React.render(React.createElement(Handler, null), document.body);
});



