import React from 'react';
import {assign, map} from 'lodash';
import GoodsForm from './GoodsForm';

export default React.createClass({

  getInitialState() {
    return assign({}, this.props);
  },

  handleSubmit(event) {
    event.preventDefault();
    this.props.onSubmit(this.state.document);
  },

  handleChange(prop, event) {
    this.state.document[prop] = event.target.value;
    this.forceUpdate();
  },

  render() {

    return (
      React.createElement("form", {onSubmit: this.handleSubmit}, 

        React.createElement("div", {className: "form-horizontal"}, 

          React.createElement("div", {className: "control-group"}, 
            React.createElement("label", null, "Bill Number"), 
            React.createElement("input", {
              value: this.state.document.number, 
              onChange: this.handleChange.bind(this, 'number')})
          ), 

          React.createElement("div", {className: "control-group"}, 
            React.createElement("label", null, "Bill Date Created"), 
            React.createElement("input", {
              value: this.state.document.date_created, 
              onChange: this.handleChange.bind(this, 'date_created')})
          )

        ), 

        React.createElement(GoodsForm, {goods: this.state.document.goods}), 

        React.createElement("button", {type: "submit"}, "Submit")
      )
    )
  }

});



