import React from 'react';
import Reflux from 'reflux';
import {map, sortBy} from 'lodash';
import GoodsItem from './GoodsItem';
import GoodsStore from '../stores/GoodsStore';
import GoodsActions from '../actions/Goods';

function getStateFromStores () {
  return {
    goods: GoodsStore.getAll(),
    total: GoodsStore.getTotal()
  }
}

export default React.createClass({

  mixins: [Reflux.ListenerMixin],

  getInitialState() {
    return getStateFromStores()
  },

  componentDidMount() {
    this.listenTo(GoodsStore, this._onChange);
  },

  addItem(event) {
    event.preventDefault();
    GoodsActions.addItem();
  },

  render() {

    var goods = map(sortBy(this.state.goods, 'id'), function (item) {
      return React.createElement(GoodsItem, {item: item, key: item.id})
    });

    return (
      React.createElement("div", {className: "goods-container"}, 
        React.createElement("ul", {className: "goods"}, goods), 
        React.createElement("div", null, "Total: ", this.state.total), 
        React.createElement("button", {onClick: this.addItem}, "Add Item")
      )
    )
  },

  _onChange() {
    this.setState(getStateFromStores())
  }

});



